<script>
export default {
  data() {
    return {};
  },
  methods:{
    open3() {
      this.$prompt('请输入产品名称：', '申请产品', {
        confirmButtonText: '提交审核',
        cancelButtonText: '放弃申请',
      }).then(({
        value
      }) => {
        this.$message({
          type: 'success',
          message: '提交成功' + value
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '放弃成功'
        });
      });
    }
  }
};
</script>

